.workflowwidget.workflow-widget {
  align-items: center;
  display: flex;
}

.workflowwidget.workflow-widget.workflow-widget--margin-right {
  margin-right: 160px;
}

.workflow-widget__button {
  align-items: center;
  background-color: transparent;
  border: 0;
  border-radius: 3px !important;
  display: flex;
  padding: 2px 5px;
  transition: opacity 150ms;
}

.workflow-widget__button svg {
  width: 24px;
}

.workflow-widget__button.workflow-widget__button--loading {
  opacity: 0.1;
}

.workflow-widget__item__icon {
  font-size: 22px;
}

.workflow-widget__item__icon.workflow-widget__item__icon--red {
  color: #d40404;
}

.workflow-widget__item__icon.workflow-widget__item__icon--blue {
  color: #005a93;
}

.workflow-widget__item__icon.workflow-widget__item__icon---light-blue {
  color: #29abe2;
}

.workflow-widget__item__icon.workflow-widget__item__icon--green {
  color: #3fa142;
}

.workflow-widget__item__icon.workflow-widget__item__icon--orange {
  color: #ff7f22;
}

.workflow-widget__item {
  display: flex;
  align-items: center;
  font-size: 1.05rem;
  min-width: 140px;
  padding: 7px 18px;
  transition: color 150ms, background-color 150ms;
}

.workflow-widget__item .workflow-widget__item__icon {
  margin-right: 12px;
}

.workflow-widget__item img {
  padding: 2px;
  border-radius: 3px;
  margin-right: 12px;
}

.workflow-widget__item.workflow-widget__item--active {
  background-color: #0071C4;
  color: white;
}

.workflow-widget__item.workflow-widget__item--active:hover {
  background-color: #0071C4;
}

.workflow-widget__item.workflow-widget__item--active img {
  background-color: white;
}

.workflow-widget__item.workflow-widget__item--active .workflow-widget__item__icon {
  color: white;
}

.workflow-widget__item.workflow-widget__item--no-state {
  color: #566b8d;
  font-style: italic;
}

.workflow-widget__item.workflow-widget__item--no-state.workflow-widget__item--active {
  color: white;
}

.workflow-widget__item:hover {
  background-color: #EEF0F4;
}

.workflow-widget__item.workflow-widget__item--link img {
  margin-right: 10px;
  padding-top: 0;
  padding-bottom: 0;
  width: 28px;
}

/**
 * Elemental actions
 */

.workflow-element-actions {
  align-items: center;
  display: flex;
}




